.covimg{
    position: relative;
    width: 50%;
}

.covcontent{
    text-align: center;
    margin-top: 350px;
    margin-left: 500px;
    position: absolute;
}
/*.covcontent button{
  margin-left: 50px;
}*/
h1{
    color: rgb(255, 255, 255);
    font-size: 50px;
}

button{
    background-color: rgb(8, 122, 151);
    color: white;
    font-size:25px;
    font-weight: bold;
    width: 400px;
    height: 50px;
    border-radius: 10px; 
    transition: all 0.3s;
    box-shadow: 0px 5px 10px rgba(darken(dodgerblue, 60%));
}

button:hover{
    cursor: pointer;
    background-color: rgba(8, 122, 151,0.5);
    box-shadow: 0px 15px 25px -5px rgba(darken(dodgerblue, 40%));
    transform: scale(1.03);
}

@media only screen and (max-width: 768px) {
  .covimg {
    max-width: 50%;
  }
  .covcontent{
    text-align: center;
    margin-left: 350px;
  }
}




  