.summary {
    text-align: center;
    max-width: 45rem;
    width: 90%;
    margin: auto;
    margin-top: 5rem;
    position: relative;
    background-color: #383838;
    color: white;
    border-radius: 14px;
    padding: 1rem;
    box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
  }
  
  .summary h2 {
    font-size: 2rem;
    margin-top: 0;
    font-weight: bold;
  }
  @media only screen and (max-width: 480px) {
    .summary {
      margin-left: 70px;
      width: 40rem;
    }
  }
  @media only screen and (max-width: 600px) {
    .summary {margin-left: 250px;}
  }

  

  