h1{
  color: black;
}
body{
  background-color: rgb(36, 35, 35);
}
.back-to-top, .feedback {
  position: fixed;
  bottom: 50px;
  right: 100px;
  font-size: 50px;
  background: rgb(8, 122, 151);
  color: white;
  cursor: pointer;
  border-radius: 100px;
  border: none;
  box-shadow: 0 5px 10px #ccc;
  width: 100px;
  padding-bottom: 90px;

}
.feedback{
  bottom: 50px;
  left: 80px;
  width: 250px;
  font-size: 20px;
  padding-bottom: 0;
}

.back-to-top:hover {
  background: rgba(8, 122, 151,0.5);
}
